.skillsContenedor {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    .skillsSection {
        display: flex;
        justify-content: space-evenly;
        max-width: 100%;
        .skillsDescription {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            width: 100%;
            p{
                padding: 0 4rem;
            }
        }
    }
}

.App {
    background-color: $principal;
}

.skillsGrid {
    max-width: 100%;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
    padding: 5rem;
}

.monedaPixel {
    img {
        width: 2rem;
    }
}

@keyframes move {
    0% {
        transform: translate(0, 0) rotate(0);
    }
    25% {
        transform: translate(-8px, -2%) rotate(-18deg);
    }
    33% {
        transform: translate(8px, -5%) rotate(18deg);
    }
    50% {
        transform: translate(-8px, -7%) rotate(-12deg);
    }
    60% {
        transform: translate(8px, -5%) rotate(18deg);
    }
    100% {
        transform: translate(0, 0) rotate(0);
    }
}

.monedaPixel:hover {
    cursor: pointer;
    animation: move 2s infinite;
}

.flipSkill {
    position: relative;
    width: 12rem;
    height: 12rem;
    transform-style: preserve-3d;
    transition: 1s cubic-bezier(0.3, 1, 1, -0.7);
}

.frontSkill img,
.backSkill img {
    filter: drop-shadow(1rem 0.8rem 0.8rem rgba(0, 0, 0, 0.5));
    max-width: 12rem;
    max-height: 12rem;
    transition: 0.5s ease;
}

.frontSkill {
    position: absolute;
    width: 100%;
    height: 100%;
    backface-visibility: hidden;
}

.backSkill {
    position: absolute;
    width: 100%;
    height: 100%;
    transform: rotateY(180deg);
    backface-visibility: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    perspective: 100;
}


@media screen and (max-width: 768px) {
    .skillsSection {
        flex-direction: column;
        align-items: center;
        justify-content: center;
        .skillsGrid {
            grid-template-columns: repeat(3, 1fr);
            gap: 1rem;
            margin: 2rem 0;
        }
        .skillsDescription{
            p{
                padding: 0;
            }
        }
    }
    .flipSkill {
        width: 8rem;
        height: 8rem;
        img{
            max-width: 100%;
            max-height: 100%;
        }
    }
    .frontSkill, .backSkill {
        backface-visibility: hidden;
    }
}
