html {
  font-size: 62.5%;
  box-sizing: border-box;
}
*, *:before, *:after {
  box-sizing: inherit;
}

body{
  background-color: $principal;
}

.noOverflowX{
  overflow-x: hidden;
}

.titulo{
  font-size: 3.2rem;
  font-family: $fuente_titulos;
  color: $secundario;
  text-shadow: 3px 3px 6px #150033;
}
.texto{
    font-family: $fuente_base;
    font-size: 1.2rem;
    font-family: $fuente_titulos;
    color: $secundario;
    text-align: justify;
    margin: 2rem;
    line-height: 2rem;
}

